import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';

export interface MenuAction {
    title: string;
    onTrigger: (params: ICellRendererParams) => void;
    getDisabled?: (data: any) => boolean;
    disabledTooltip?: string;
}

export interface AtuiMenuCellParams extends ICellRendererParams {
    icon?: string;
    actions: MenuAction[] | ((params: ICellRendererParams) => MenuAction[]);
}

/**
 * @category Table Cell
 * @description A cell component for displaying a menu.
 */

@Component({
    tag: 'atui-menu-cell',
    shadow: false,
})
export class AtuiMenuCellComponent implements ICellRendererComp {
    @Element() el;
    @State() params: AtuiMenuCellParams;

    init(params: AtuiMenuCellParams): void {
        this.params = params;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(_): boolean {
        return false;
    }

    render() {
        const actions =
            typeof this.params.actions === 'function'
                ? this.params.actions(this.params)
                : this.params.actions;

        return (
            <Host class="flex h-full items-center gap-4">
                <atui-menu
                    width="auto"
                    portal={true}
                >
                    <atui-button
                        type="secondaryText"
                        icon={this.params.icon ?? 'more_vert'}
                        slot="menu-trigger"
                    />
                    <div
                        slot="menu-content"
                        class="flex flex-col"
                    >
                        {actions &&
                            actions.map(
                                (action) =>
                                    typeof action === 'object' &&
                                    (action.getDisabled &&
                                    action.getDisabled(this.params.data) &&
                                    action.disabledTooltip ? (
                                        <atui-tooltip>
                                            <div
                                                slot="tooltip-trigger"
                                                class="w-full"
                                            >
                                                <atui-menu-item
                                                    label={action.title}
                                                    disabled={true}
                                                    onClick={() => {
                                                        if (
                                                            action.onTrigger !==
                                                            undefined
                                                        ) {
                                                            action.onTrigger(
                                                                this.params,
                                                            );
                                                        }
                                                    }}
                                                ></atui-menu-item>
                                            </div>
                                            <span slot="tooltip-content">
                                                {action.disabledTooltip}
                                            </span>
                                        </atui-tooltip>
                                    ) : (
                                        <atui-menu-item
                                            label={action.title}
                                            disabled={
                                                action.getDisabled
                                                    ? action.getDisabled(
                                                          this.params.data,
                                                      )
                                                    : false
                                            }
                                            onClick={() => {
                                                if (
                                                    action.onTrigger !==
                                                    undefined
                                                ) {
                                                    action.onTrigger(
                                                        this.params,
                                                    );
                                                }
                                            }}
                                        ></atui-menu-item>
                                    )),
                            )}
                    </div>
                </atui-menu>
            </Host>
        );
    }
}
